
<template>
  <div @click="clickQrCode">
    <vue-qr
      :text="text"
      :margin="0"
      :logoMargin="3"
      :correctLevel="3"
      :logoSrc="
        isTimeOut
          ? require('../assets/image/visitor/change.svg')
          : require('../../public/favicon.svg')
      "
      :logoScale="logoScale"
      :size="size"
    />
  </div>
</template>

<script>
import vueQr from 'vue-qr'
export default {
  name: 'QrCode',
  data() {
    return {
      timer: null
    }
  },
  props: {
    //二维码内容
    text: {
      type: String,
      require: true
    },
    //二维码刷新图标显示时间，Number类型。可不传，则不显示刷新图标
    time: {
      type: Number,
      require: false
    },
    //二维码是否超时，超时显示刷新图标
    isTimeOut: {
      type: Boolean,
      require: false,
      default: false
    },
    //二维码大小，Number类型。可不传，默认200
    size: {
      type: Number,
      require: false,
      default: 200
    },
    //二维码中间图标大小，Number类型。可不传，默认0.2
    logoScale: {
      type: Number,
      require: false,
      default: 0.2
    }
  },
  methods: {
    clickQrCode() {
      this.$emit('click')
      this.refreshQrCode()
    },
    refreshQrCode() {
      if (this.time) {
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this.$emit('update:isTimeOut', true)
        }, this.time)
      }
    }
  },
  created() {
    this.refreshQrCode()
  },
  components: {
    vueQr
  }
}
</script>

