

<template>
  <div class="pass-code">
    <van-image
      width="100%"
      fit="fill"
      :src="require('../../assets/image/home/top.png')"
      alt="图片加载失败"
    />
    <div class="code-page">
      <div class="pass-code-info">
        <span>被访人:</span>
        <span>
          {{ data.name }}
        </span>
      </div>
      <div class="pass-code-info">
        <span>拜访公司:</span>
        <span>
          <!-- {{ data.companyName }} -->
          <!-- 暂时写死 -->
          湖北公众信息产业有限责任公司
        </span>
      </div>
      <div class="pass-code-info">
        <span>访客:</span>
        <span>
          <!-- 访客姓名字段也为name外套一层visitorPersonArr -->
          {{ data.visitorName }}
        </span>
      </div>
      <div class="pass-code-info">
        <span>状态:</span>
        <van-tag v-if="data.tagStatus == 1" type="success" size="medium">
          {{ '生效中' }}
        </van-tag>
        <van-tag v-if="data.tagStatus == 0" type="danger" size="medium">
          {{ '已失效' }}
        </van-tag>
      </div>
      <div class="pass-code-info">
        <span>有效期:</span>
        <span> {{ data.visitStartTime }} - {{ data.visitEndTime }} </span>
      </div>
      <QrCode
        class="qr-code"
        :text="text"
        :size="size"
        :isTimeOut.sync="isTimeOut"
        @click="init()"
      />
      <!-- :time="60 * 1000" -->
      <div class="tips-info">
        <span>1.可授权人脸识别、车牌识别及二维码识别通行。</span>
        <br />
        <span>2.请在有效期内通行，超时后通行权限失效！</span>
        <br />
        <span>3.通行权限失效后，请联系被访人重新获取权限。</span>
      </div>
    </div>
    <div class="footer-placeholder" />
  </div>
</template>
<script>
import QrCode from '@/components/QrCode.vue'
import { qryVisitorInfoDetail } from '@/api/visitor' // 获取通行码

import { mapState } from 'vuex'
export default {
  name: 'PassCode',
  data() {
    return {
      text: '',
      size: 250,
      isTimeOut: false,
      data: {}
    }
  },
  methods: {
    init() {
      const visitorParams = this.$util.getRouteParams(
        this.$route,
        1 * 60 * 60 * 1000
      ) // 获取页传入的参数 or 设置参数
      if (visitorParams) {
        this.$toast.loading({
          forbidClick: true,
          duration: 0,
          message: '数据加载中...'
        })
        qryVisitorInfoDetail(visitorParams).then((res) => {
          this.$toast.clear()
          if (res.code === 200) {
            const data = res.data.row[0]
            const visitorPersonArr = eval('(' + data.visitorPersonArr + ')')[0]
            data.visitorName = visitorPersonArr.visitorName
            if (
              //链接是否在有效期内
              this.$util.isDuringDate(data.visitStartTime, data.visitEndTime)
            ) {
              this.text = visitorPersonArr.qrCode || '该二维码code未生成'
              this.isTimeOut = false
              this.data = {
                ...data,
                tagStatus: 1,
                visitStartTime: this.$util.format(
                  new Date(data.visitStartTime.replace(/-/g, '/')),
                  'yyyy-MM-dd hh:mm'
                ),
                visitEndTime: this.$util.format(
                  new Date(data.visitEndTime.replace(/-/g, '/')),
                  'yyyy-MM-dd hh:mm'
                )
              }
            } else {
              this.data.tagStatus = 0
              this.text = '链接已过期'
              this.$toast.fail({
                duration: 0,
                forbidClick: true,
                message: '链接已过期'
              })
            }
          } else {
            this.data.tagStatus = 0
            this.text = res.msg
            this.$toast({
              duration: 0,
              forbidClick: true,
              message: res.msg
            })
          }
        })
      } else {
        this.$router.push('/404')
      }
    }
  },
  computed: {
    ...mapState({
      myUserInfo: (state) => state.user.userInfo // 用户信息
    })
  },
  created() {
    if (window.innerWidth > 500) this.size = 300 //动态设置二维码大小
    this.init()
  },
  components: {
    QrCode
  }
}
</script>

<style lang="less" scoped>
.code-page {
  position: relative;
  margin: -70px 10px 20px;
  background: #ffffff;
  box-shadow: 0 0 10px -7px #cac7c7;
  border-radius: 5px;
  padding: 10px 15px 20px;
  .pass-code-info {
    font-size: 13px;
    line-height: 37px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    > span:first-child {
      color: #8d8484;
    }
    > span:last-child {
      font-weight: 600;
    }
    .van-tag {
      height: 25px;
    }
  }
  .qr-code {
    display: flex;
    justify-content: center;
    margin: 12px 0;
  }
  .tips-info {
    padding-top: 15px;
    font-size: 11px;
    line-height: 30px;
    letter-spacing: 2px;
    color: #a7a5a5;
  }
  .footer-placeholder {
    height: 60px;
  }
}
</style>

